h1{
  font-size:30px;
}

.Header{
  position:fixed;
  width:100%;
  padding-left:5%;
}

.contact-float-right{
  position:fixed;
  right:5%;
  top:5%;
  list-style:none;
}

@media (min-width: 1500px){
  .contact-float-right{
    right:20%;

  }

}


.li-contact{
  position: relative;
  width: 25px;
  height: 25px;
  padding: 10px;
  margin-bottom: 2em;
  border: solid black 1px;
  border-radius: 50%;
  background-position: center;
  background-size: 20px;
  background-repeat: no-repeat;
  cursor: pointer;
}

.li-contact:hover{
  top:-5px;
  -webkit-filter: invert(1);
          filter: invert(1);
  background-color:white;
  -webkit-animation-name: upli;
          animation-name: upli;
  -webkit-animation-duration:0.2s;
          animation-duration:0.2s;
}

@-webkit-keyframes upli{
  from{
    top: 0;
  }
  to{
    top:-5px;
    background-color:white;
  }
}

@keyframes upli{
  from{
    top: 0;
  }
  to{
    top:-5px;
    background-color:white;
  }
}

